.loader-wrapper {
  position: relative;
  height: 100px;
  width: 100; }
  .loader-wrapper svg {
    position: absolute;
    left: 50%;
    right: 0;
    transform: translateX(-50%); }
    .loader-wrapper svg:first-of-type {
      transform: rotate(-180deg) translateX(50%);
      bottom: 20px;
      animation: spinner1 0.5s ease-in-out 0s infinite reverse none; }
    .loader-wrapper svg:last-of-type {
      animation: spinner2 0.5s ease-in-out 0s infinite reverse none; }

@keyframes spinner1 {
  25% {
    transform: translateX(-50%) rotate(-90deg); }
  50% {
    transform: translateX(-50%) rotate(0deg); }
  75% {
    transform: translateX(-50%) rotate(90deg); }
  100% {
    transform: translateX(-50%) rotate(180deg); } }

@keyframes spinner2 {
  25% {
    transform: translateX(-50%) rotate(-90deg); }
  50% {
    transform: translateX(-50%) rotate(-180deg); }
  75% {
    transform: translateX(-50%) rotate(-270deg); }
  100% {
    transform: translateX(-50%) rotate(-360deg); } }
